body {
  background: -webkit-gradient(linear, left top, right top, from(#79bc95), to(#a4b6a5));
  background: -webkit-linear-gradient(left, #79bc95, #a4b6a5);
  background: -o-linear-gradient(left, #79bc95, #a4b6a5);
  background: linear-gradient(to right, #79bc95, #a4b6a5);
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

a {
  border: none;
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}



